import React from 'react';
import Input from './input';

const SubscriptionForm = () => {
    return (
        <>
            <form>
                <Input
                    name={'nom'}
                    label={'Nom'}
                    type={'text'}
                />
                <Input
                    name={'prenom'}
                    label={'Prenom'}
                    type={'text'}
                />
                <Input
                    name={'entreprise'}
                    label={'Entreprise'}
                    type={'text'}
                />
                <Input
                    name={'adresse'}
                    label={'Adresse'}
                    type={'text'}
                />
                <Input
                    name={'NPA'}
                    label={'NPA'}
                    type={'text'}
                />
                <Input
                    name={'localite'}
                    label={'Localité'}
                    type={'text'}
                />
                <Input
                    name={'Email'}
                    label={'Email'}
                    type={'email'}
                />
                <Input
                    name={'Nombre de participant'}
                    label={'Nombre de participant'}
                    type={'number'}
                />
                <Input
                    name={'telephone'}
                    label={'Telephone'}
                    type={'number'}
                />
            </form>
            <button>S'inscrire</button>
        </>

    );
};

export default SubscriptionForm;